/* Enhanced CV Download Component Styles */
.cv-download-section {
  text-align: center;
  max-width: 700px;
  margin: 2rem auto;
  position: relative;
  background: rgba(18, 26, 42, 0.9);
  border: 2px solid rgba(0, 247, 255, 0.3);
  border-radius: 25px;
  padding: 2.5rem;
  backdrop-filter: blur(20px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

.cv-download-section::before {
  content: '';
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  background: linear-gradient(45deg, var(--cosmic-purple), var(--neon-blue), var(--cosmic-purple));
  border-radius: 25px;
  z-index: -1;
  opacity: 0.4;
  animation: borderGlow 3s ease-in-out infinite alternate;
}

@keyframes borderGlow {
  0% { opacity: 0.4; }
  100% { opacity: 0.8; }
}

.cv-header {
  margin-bottom: 2.5rem;
}

.cv-icon-wrapper {
  position: relative;
  display: inline-block;
  margin-bottom: 1.5rem;
}

.cv-icon {
  font-size: 4rem;
  background: var(--gradient-cosmic);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: documentFloat 4s ease-in-out infinite;
  position: relative;
  z-index: 2;
}

.cv-pulse-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  border: 2px solid var(--neon-blue);
  border-radius: 50%;
  opacity: 0.6;
  animation: pulseRing 2s ease-out infinite;
}

@keyframes documentFloat {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  25% { transform: translateY(-8px) rotate(2deg); }
  50% { transform: translateY(-5px) rotate(0deg); }
  75% { transform: translateY(-12px) rotate(-2deg); }
}

@keyframes pulseRing {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.4);
    opacity: 0;
  }
}

.cv-header h3 {
  font-size: 1.8rem;
  color: var(--neon-blue);
  margin-bottom: 1rem;
  text-shadow: 0 0 15px rgba(0, 247, 255, 0.4);
}

.cv-description {
  font-size: 1rem;
  line-height: 1.6;
  opacity: 0.9;
  max-width: 500px;
  margin: 0 auto;
  color: #e0e0e0;
}

/* CV Preview Section */
.cv-preview {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 2rem;
  margin: 2rem 0;
  border: 1px solid rgba(0, 247, 255, 0.2);
  position: relative;
  overflow: hidden;
}

.cv-preview::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 247, 255, 0.1), transparent);
  animation: scanLine 3s ease-in-out infinite;
}

@keyframes scanLine {
  0% { left: -100%; }
  100% { left: 100%; }
}

.cv-preview-header {
  margin-bottom: 1.5rem;
}

.cv-preview-header h4 {
  color: var(--neon-blue);
  font-size: 1.3rem;
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 247, 255, 0.3);
}

.cv-highlights {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.highlight-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.highlight-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: rgba(0, 247, 255, 0.08);
  border: 1px solid rgba(0, 247, 255, 0.15);
  border-radius: 12px;
  transition: all 0.3s ease;
  text-align: left;
}

.highlight-item:hover {
  background: rgba(0, 247, 255, 0.15);
  border-color: var(--neon-blue);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 247, 255, 0.2);
}

.highlight-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
  background: var(--gradient-cosmic);
  padding: 0.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.highlight-text {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.highlight-text strong {
  color: var(--neon-blue);
  font-size: 0.9rem;
  font-weight: 600;
}

.highlight-text span:last-child {
  font-size: 0.8rem;
  opacity: 0.8;
  color: #c0c0c0;
}

/* Enhanced Download Button */
.cv-download-area {
  margin: 2.5rem 0;
}

.cv-download-button {
  position: relative;
  background: var(--gradient-cosmic);
  border: none;
  border-radius: 20px;
  padding: 1.2rem 2.5rem;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 247, 255, 0.3);
  margin-bottom: 1.5rem;
  min-width: 220px;
}

.cv-download-button:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 15px 35px rgba(0, 247, 255, 0.5);
}

.cv-download-button:active {
  transform: translateY(-2px) scale(1.02);
}

.button-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 2;
}

.download-icon {
  font-size: 1.5rem;
  animation: downloadBounce 2s ease-in-out infinite;
}

@keyframes downloadBounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-5px); }
  60% { transform: translateY(-3px); }
}

.button-text {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.main-text {
  font-size: 1.1rem;
  font-weight: bold;
  color: var(--space-black);
  line-height: 1.2;
}

.sub-text {
  font-size: 0.8rem;
  color: rgba(10, 14, 23, 0.8);
  line-height: 1;
}

.button-shine {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.6s ease;
}

.cv-download-button:hover .button-shine {
  left: 100%;
}

/* File Info Section */
.cv-file-info {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin-top: 1.5rem;
}

.file-detail {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: #c0c0c0;
  opacity: 0.8;
}

.detail-icon {
  font-size: 1rem;
  color: var(--neon-blue);
}

.cv-footer-note {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(0, 247, 255, 0.2);
  opacity: 0.8;
  font-style: italic;
}

.cv-footer-note small {
  color: var(--neon-blue);
  opacity: 0.9;
  line-height: 1.5;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .cv-download-section {
    margin: 1.5rem 1rem;
    padding: 2rem;
  }
  
  .cv-preview {
    padding: 1.5rem;
  }
  
  .highlight-row {
    grid-template-columns: 1fr;
    gap: 0.8rem;
  }
  
  .highlight-item {
    padding: 0.8rem;
  }
  
  .highlight-icon {
    font-size: 1.3rem;
    padding: 0.4rem;
  }
  
  .cv-download-button {
    padding: 1rem 2rem;
    min-width: 200px;
  }
  
  .button-content {
    gap: 0.8rem;
  }
  
  .cv-file-info {
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .cv-download-section {
    margin: 1rem 0.5rem;
    padding: 1.5rem;
  }
  
  .cv-icon {
    font-size: 3rem;
  }
  
  .cv-header h3 {
    font-size: 1.5rem;
  }
  
  .cv-description {
    font-size: 0.9rem;
  }
  
  .cv-preview {
    padding: 1rem;
  }
  
  .highlight-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .cv-download-button {
    width: 100%;
    padding: 1rem;
  }
  
  .button-content {
    justify-content: center;
  }
  
  .button-text {
    text-align: center;
  }
}
